import React from "react"
import "./FAQ.css"

function Faq(){
    return(
        <div className = "centrage">
            
			  <h2>Foire aux questions - FAQ</h2>


			  <p styleClass = "question">-Est-ce que le stage est obligatoire?</p> 
               
				 
			  <p styleClass = "reponse">Le stage de fin d'tudes en informatique est obligatoire 
			  pour l'obtention du diplme collggial.
              </p>

              <br />
              <p styleClass = "question"> -Quel est l'horaire de l'tudiant durant les stages?</p>
				 
			  <p styleClass = "reponse"> L'tudiant doit respecter l'horaire de l'entreprise durant son stage. </p>


              <br />
			    <p styleClass = "question">-Est-ce que l'tudiant travaille pendant les journes pdagogiques et
			  les journes de rattrapage?
				 
              </p>

			  <p styleClass = "reponse"> L'tudiant doit respecter l'horaire de l'entreprise durant son stage et ce mme
			  durant les journes pdagogiques et de rattrapage. </p>



              <br />
			   <p styleClass = "question"> -Quelle est la dure d'un stage de fin d'tudes?</p>
				 
			   <p styleClass = "reponse">La dure du stage est de 15 semaines pour les deux profils de sortie (rseaux et programmation). </p>


               <br />

			    <p styleClass = "question">-Quelles sont les dates prvues pour les stages?</p>
				 
                <p styleClass = "reponse"> Les stages sont prvus du 21 janvier au 3 mai 2019. </p>


        </div>
        
    )
}

export default Faq